import React, { useState, useEffect } from 'react';
import '../componentsCSS/Battalions.css';
import { useNavigate, useLocation } from 'react-router-dom';
import units from '../data/Units';
import StepsBtnBattelions from '../componentsJS/StepsBtnBattelions';

function Battalions() {
    const navigate = useNavigate();
    const location = useLocation();
    const unitName = location.state?.unitName || "";
   const buttonTexts = [
        '1',
        '2-3',
        '4',
        '5-9'
    ];
    //     return ,     
    const unit = units.find(unit => unit.name === unitName);

    const [currentStep, setCurrentStep] = useState(() => {
        //     -sessionStorage,  ,   -1
        const savedStep = sessionStorage.getItem('currentStep');
        return savedStep ? Number(savedStep) : 1; //    ,  -1
    });

    //    unit,   
    if (!unit) {
        return <div>  </div>; //     
    }

    //       
    const handleStepChange = (stepNumber) => {
        setCurrentStep(stepNumber); //   
        sessionStorage.setItem('currentStep', stepNumber); //  -sessionStorage
    };

    return (
        <div className="Battalions">
            <div className='white-circle-Battalions'>
                <h1 className="titleBattalions">{unitName}</h1>
                <StepsBtnBattelions
                    numSteps={unit.steps.length}
                    currentStep={currentStep} //    
                    onStepChange={handleStepChange} //     
                />
                {/*        div */}
                <div className="card-content">
                <h1 className="card-title">{`   ${buttonTexts[currentStep - 1]}`}</h1>
                <p className='text-batel'> {unit.steps[currentStep - 1]} </p>{/*     */}
                </div>
                <div className='navigation-btn-Bat'>
                    <div onClick={()=>navigate("/emergency")} className='prevBat'>
                        <p>  </p>
                    <img className="arrowBat leftBat" src={`${process.env.PUBLIC_URL}/assets/imgs/arrow.svg`}/>    
                          </div>
                    <div onClick={()=>navigate("/war")} className='nextBat'>
                    <p> </p>

                    <img className="arrowBat rightBat" src={`${process.env.PUBLIC_URL}/assets/imgs/arrow.svg`}/>    

                    </div>

                </div>
            </div>
        </div>
    );
}

export default Battalions;
