import React from 'react';
import { useNavigate } from 'react-router-dom';
import '../componentsCSS/ExcelMinala.css';

function ExcelMinala() {
    const navigate = useNavigate();
    const imgSrc = `${process.env.PUBLIC_URL}/assets/imgs/excel.png`;

    const handleImageClick = () => {
        navigate('/MagnifyPic', { state: { imagePath: imgSrc, situation: "-" } });
    };

    //        
    const buttons = [
        { text: ' ', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=2028248870#gid=2028248870' },
        { text: '', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=523534797#gid=523534797' },
        { text: '', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=433640630#gid=433640630' },
        { text: '', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=538348602#gid=538348602' },
        { text: '', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=2139529790#gid=2139529790' },
        { text: '"', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=1906463900#gid=1906463900' },
        { text: ' ', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=1061139287#gid=1061139287' },
        { text: ' ', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=19746556#gid=19746556' },
        { text: '  ', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=2062063032#gid=2062063032' },
        { text: '"', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=222264061#gid=222264061' },
        { text: '', link: 'https://docs.google.com/spreadsheets/d/1nP8Ob4OTI9ydzYqWIP-NKE3FpY6wrPkv/edit?gid=1077980303#gid=1077980303' }
    ];

    return (
        <div className="ExcelMinala">
            <div className='white-circle-ExcelMinala'>
                <h1 className="titleExcelMinala"> </h1>
                <h1 className="excel-Titles"> :</h1>
                <p className="excel-explanations">
                              
                </p>
                <h1 className="excel-Titles">:</h1>
                <p className="excel-explanations">
                           ,    
                </p>
                <h1 className="excel-Titles"> :</h1>
                <p className="excel-explanations">
                .      ,   '  .
                .  '      ",    .
                .     ,       .
                .      .
                </p>
                <img
                    src={imgSrc}
                    className="excel2Img"
                    alt="excel Diagram"
                    onClick={handleImageClick}
                />
                <div className='Wartext-img'>    </div>

                <h1 className="excel-Titles">   :</h1>
                <p className="excel-explanations">
                /               
                </p>

                <div className="buttons-containerExcel">
                    {buttons.map((button, index) => (
                        <button 
                            key={index} 
                            className="excel-btn" 
                            onClick={() => window.open(button.link, '_blank', 'noopener,noreferrer')}
                        >
                            {button.text}
                        </button>
                    ))}
                </div>

                <div className='navigation-btn-Excel'>
                    <div onClick={() => navigate("/war")} className='prevExcel'>
                        <p> </p>
                        <img className="arrow leftExcel" src={`${process.env.PUBLIC_URL}/assets/imgs/arrow.svg`} />
                    </div>
                    <div onClick={() => navigate("/validation")} className='nextExcel'>
                        <p> </p>
                        <img className="arrow rightExcel" src={`${process.env.PUBLIC_URL}/assets/imgs/arrow.svg`} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ExcelMinala;
