import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import '../componentsCSS/MagnifyPic.css';

function MagnifyPic() {
    const location = useLocation();
    const navigate = useNavigate();
    const { imagePath, situation } = location.state || {}; //   state,   

    const [isLandscape, setIsLandscape] = useState(false);

    useEffect(() => {
        function checkOrientation() {
            if (window.innerWidth > window.innerHeight) {
                setIsLandscape(true);
            } else {
                setIsLandscape(false);
            }
        }

        checkOrientation(); //  
        window.addEventListener("resize", checkOrientation);

        return () => {
            window.removeEventListener("resize", checkOrientation);
        };
    }, []);

    if (!imagePath) {
        return <div>Error: No image provided for magnification</div>;
    }

    //  -class    
    const imageClass = isLandscape 
        ? (situation === "" ? "magnified-image-nafa-landscape" :
            situation === "" ? "magnified-image-gdud-landscape" :
            situation === "" ? "magnified-image-mahoz-landscape" :
            situation === "-" ? "magnified-image-validation-landscape" : "")
        : (situation === "" ? "magnified-image-nafa-portrait" :
            situation === "" ? "magnified-image-gdud-portrait" :
            situation === "" ? "magnified-image-mahoz-portrait" :
            situation === "-" ? "magnified-image-validation-portrait" : "");

    //  -class  close
    const closeBtnClass = situation === "" ? "close-btn-nafa" : isLandscape ? "close-btn-landscape" : "close-btn-portrait";

    return (
        <div className="MagnifyPic">
            <button className={`close-btn ${closeBtnClass}`} onClick={() => navigate(-1)}>
                x
            </button>
            <img src={imagePath} alt="Magnified" className={imageClass} />
        </div>
    );
}  

export default MagnifyPic;
